import React, { useState } from 'react';
import Main from '../tempate/main'
import * as moment from 'moment'
import { makeData, Logo, Tips } from "../Utils";
import matchSorter from "match-sorter";

import Select from "react-select";

// Import React Table
import ReactTable from "react-table";

import axios from 'axios'

const headerProps = {
    icon: 'users',
    title: 'usuários',
    subtitle: 'Cadastro de usuário'
}

const baseUrl = 'http://localhost:3001/user'

const initState = {
    user: { first_name: '', last_name: '', email: '', gender: '', language: '', avatar: '', birthday: ''  },
    list: []
}

export default class UserCrud extends React.Component {

    state = {...initState}
    

    UNSAFE_componentWillMount(){
        axios(baseUrl).then(resp => {
            this.setState({ list: resp.data })
        })
    }

    clear(){
        this.setState({ user: initState.user })
    }

    

    save(){
        const user = this.state.user
        const method = user.id ? 'put' : 'post'
        const url = user.id ? `${baseUrl}/${user.id}` : baseUrl

        axios[method](url,user)
        .then(resp => {
            const list = this.getUpdatedList(resp.data)
            this.setState({ user: initState.user, list })
        })
    }
    getUpdatedList(user){
        const list = this.state.list.filter(u => u.id !== user.id)
        list.unshift(user)
        return list
    }
    updatefield(event){
        const user = { ...this.state.user }
        user[event.target.name] = event.target.value
        this.setState({ user })   
    }

    renderForm(){
        return (
            <div className="form">
                <div className="row">
                    <div className="col-12 col-md-6">
                        <div className="form-group">
                            <label htmlFor="FirstName">First Name</label>
                            <input type="text" className="form-control"
                                name="first_name"
                                value={this.state.user.first_name}
                                onChange={e => this.updatefield(e)}
                                placeholder="Enter your First Name"
                                />
                        </div>
                        <div className="form-group">
                            <label htmlFor="name">Last Name</label>
                            <input type="text" className="form-control"
                                name="last_name"
                                value={this.state.user.last_name}
                                onChange={e => this.updatefield(e)}
                                placeholder="Enter your Last Name"
                            />
                        </div>
                    </div>
                    <div className="col-12 col-md-6">
                        <div className="form-group">
                            <label htmlFor="name">Email</label>
                            <input type="email" className="form-control"
                                name="email"
                                value={this.state.user.email}
                                onChange={e => this.updatefield(e)}
                                placeholder="Enter your Email"
                                />
                        </div>
                        <div className="form-group">
                            <label htmlFor="name">Birthday</label>
                            <input type="date" className="form-control"
                                name="birthday"
                                value={this.state.user.birthday}
                                onChange={e => this.updatefield(e)}
                                placeholder="Enter your Birthday"
                            />
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-12 col-md-6">
                        <div className="form-group">
                            <label htmlFor="name">Gender</label>
                            <select className="form-select form-control"
                                name="gender"
                                value={this.state.user.gender}
                                onChange={e => this.updatefield(e)}
                                placeholder="Enter your Gender" aria-label="Default select example">
                                    <option value="">None</option>
                                    <option value="M">Masculino</option>
                                    <option value="F">Feminino</option>
                                </select>
                        </div>
                    </div>
                    <div className="col-12 col-md-6">
                        <div className="form-group">
                            <label htmlFor="name">Language</label>
                            <input type="text" className="form-control"
                                name="language"
                                value={this.state.user.language}
                                onChange={e => this.updatefield(e)}
                                placeholder="Enter your Language"
                                />
                        </div>
                    </div>
                </div>
                <hr/>
                <div className="row">
                    <div className="col-12 d-flex justify-content end">
                        <button className="btn btn-primary"
                        onClick={e => this.save(e)}>Salvar</button>
                        <button className="btn btn-secondary ml-2"
                        onClick={e => this.save(e)}>Cancelar</button>
                    </div>
                </div>
            </div>
        );
    }
    load(user){
        this.setState({ user })
    }
    remove(user){
        axios.delete(`${baseUrl}/${user.id}`).then(resp => {
            const list = this.state.list.filter(u => u !== user)
            this.setState({ list })
        })
    }

    birthday = (e) => {
        this.setState({birthday: e.target})
        this.onFilteredChangeCustom(
            this.state.list.map(o => {
                return o.value;
            }),
            "birthday"
            ); 
    }

    

    gender = (e) => {
        this.setState({ gender: e.target });
        this.onFilteredChangeCustom(
        this.state.list.map(o => {
            return o.value;
        }),
        "gender"
        ); 
        
    }

    language = (e) => {
        this.setState({language: e.target})
        this.onFilteredChangeCustom(
            this.state.list.map(o => {
                return o.value;
            }),
            "language"
            ); 
        
    }   
    
    onFilteredChangeCustom = (value, accessor) => {
        let filtered = this.state.filtered;
        let insertNewFilter = 1;

        // if(filtered.length){
        //     filtered.forEach((filter, i) => {
        //         if(filter["id"] === accessor){
        //             if(value === "" || !value.length) filtered.splice(i, 1);
        //             else filter["value"] = value;

        //             insertNewFilter = 0;
        //         }
        //     });
        // }

        // if(insertNewFilter){
        //     filtered.push({id: accessor, value: value });
        // }

        this.setState({ filtered: filtered });
    }
    
    renderTable(){
        const { data } = this.state;
        return (
            <table 
            data={data}
            checkboxSelection
            filterable
            filtered={this.state.filtered} 
            onFilteredChange={(filtered, column, value) => {
                this.onFilteredChangeCustom(value, column.id || column.accessor);
            }}
            defaultFilterMethod={(filter, row, column) => {
                const id = filter.pivotId || filter.id;
                if (typeof filter.value === "object") {
                  return row[id] !== undefined
                    ? filter.value.indexOf(row[id]) > -1
                    : true;
                } else {
                  return row[id] !== undefined
                    ? String(row[id]).indexOf(filter.value) > -1
                    : true;
                }
              }}
              columns={[
                {
                  Header: "Name",
                  columns: [
                    {
                      Header: "First Name",
                      accessor: "firstName"
                    },
                    {
                      Header: "Last Name",
                      id: "lastName",
                      accessor: d => d.lastName
                    }
                  ]
                },
                {
                  Header: "Info",
                  columns: [
                    {
                      Header: "Age",
                      accessor: "age"
                    },
                    {
                      Header: "Over 21",
                      accessor: "age",
                      id: "over",
                      Cell: ({ value }) => (value >= 21 ? "Yes" : "No"),
                      filterMethod: (filter, row) => {
                        if (filter.value.indexOf("all") > -1) {
                          return true;
                        }
                        if (filter.value.indexOf("true") > -1) {
                          return row[filter.id] >= 21;
                        }
                        return row[filter.id] < 21;
                      },
                      Filter: ({ filter, onChange }) => {
                        return (
                          <select
                            onChange={event => {
                              let selectedOptions = [].slice
                                .call(event.target.selectedOptions)
                                .map(o => {
                                  return o.value;
                                });
    
                              this.onFilteredChangeCustom(selectedOptions, "over");
                            }}
                            style={{ width: "100%" }}
                            value={filter ? filter.value : ["all"]}
                            multiple={true}
                          >
                            <option value="all">Show All</option>
                            <option value="true">Can Drink</option>
                            <option value="false">Can't Drink</option>
                          </select>
                        );
                      }
                    }
                  ]
                }
              ]}
              
            id="example2" 
            className="table mt-4"
            
            >
                <pre>{JSON.stringify(this.state.filtered, null, 2)}</pre>
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th>
                        <Select
                            style={{ width: "100px", marginBottom: "20px" }}
                            name="gender"
                            onChange={e => this.gender(e)}
                            value={this.state.gender}
                            multi={true}
                            options={this.state.list.map((o, i) => {
                                return { id: i, value: o.gender, label: o.gender };
                            })}
                        />
                            
                        </th>
                        <th>
                        <Select 
                                style={{ width: "100px", marginBottom: "20px" }}
                                name="language"
                                value={this.state.language}
                                multi={true}
                                onChange={e => this.language(e)}
                                options={this.state.list.map((o, i) => {
                                    return { id: i, value: o.language, label: o.language };
                                })}
                        />
                        </th>
                        <th></th>
                        <th>
                            <Select 
                                style={{ width: "100px", marginBottom: "20px" }}
                                name="birthday"
                                value={this.state.birthday}
                                multi={true}
                                onChange={e => this.birthday(e)}
                                options={this.state.list.map((o, i) => {
                                    return { id: i, value: o.birthday, label: o.birthday };
                                })}
                            />
                        </th>
                        <th></th>
                        <th></th>
                    </tr>
                    <tr>
                        <th>Id</th>
                        <th>First Name</th>
                        <th>Last Name</th>
                        <th>E-mail</th>
                        <th>Gender</th>
                        <th>Language</th>
                        <th>Avatar</th>
                        <th>Birthday</th>
                        <th>Edit Contact</th>
                        <th>Remover Contact</th>
                    </tr>
                </thead>
                <tbody>
                   {this.renderRows()}
                </tbody>
            </table>
        );
    }

    renderRows(){
        return this.state.list.map((o, i) => {
            
            return (
                <tr key={i}>
                    <td>{o.id}</td>
                    <td>{o.first_name}</td>
                    <td>{o.last_name}</td>
                    <td>{o.email}</td>
                    <td>{o.gender}</td>
                    <td>{o.language}</td>
                    <td><img src={o.avatar}/></td>
                    <td>{o.birthday}</td>
                    <td>
                        <button className="btn btn-warning mr-2"
                        onClick={() => this.load(o)}>
                            <i className="fa fa-pencil"></i>
                        </button>
                    </td>
                    <td>
                        <button className="btn btn-danger"
                        onClick={() => this.remove(o)}>
                            <i className="fa fa-trash"></i>
                        </button>
                    </td>
                </tr>
            );
        })
    }

    render(){
       return(
           
        <Main {...headerProps}>
            {this.renderForm()}
            {this.renderTable()}
        </Main>        
        ); 
    }

    
}