import React from "react";
import namor from "namor";

const range = len => {
    const arr = [];
    for(let i = 0; i < len; i++){
        arr.push(i);
    }
    return arr;
};

const newPerson = () => {

    

    const statusChange = Math.random();
    return {
        firstName: namor.generate({ words: 1, numbers: 0}),
        lastName: namor.generate({ words: 1, numbers: 0}),
        email: namor.generate({ words: 1, numbers: 0}),
        gender: namor.generate({ words: 1, numbers: 0}),
        language: namor.generate({ words: 1, numbers: 0}),
        avatar: namor.generate({ words: 1, numbers: 0}),
        birthday: namor.generate({ words: 1, numbers: 0}),
    };
}

export function makeData(len = 200){
    return range(len).map(d => {
        return {
            ...newPerson(),
            children: range(10).map(newPerson)
        };
    });
}

export const Logo = () => (
    <div
        style={{
            margin: "1rem auto",
            display: "flex",
            flexWrap: "wrap",
            alignItems: "center",
            justifyContent: "center"
        }}
    />
);